
/**
 * 变更类型
 */
export class ChangeDetailType {

  /**
   * 新增
   */
  static Added = 'Added';

  /**
   * 修改
   */
  static Modify = 'Modify';

  /**
   * 删除
   */
  static Deleted = 'Deleted';
}

/**
 * 行变更信息
 * 必须包含：
 * 1、DataId   => 主键值；
 * 2、属性名   => 新的属性值；
 * 3、子表名+s => 子表行的ChangeDetail数组
 */
export interface ChangeDetailInfo {
  DataId?: string;
  [key: string]: number | string | boolean | null | ChangeDetail | ChangeDetail[] | { [key: string]: any };
}

/**
 * 行变更详情
 * 包含：
 * 1、变更类型；
 * 2、变更信息
 */
export interface ChangeDetail {
  ChangeType: ChangeDetailType;
  ChangeInfo: ChangeDetailInfo;
}


/**
 * 请求类型
 */
export interface RequestInfo {
  dataChange: ChangeDetail[];
  variableChange?: ChangeDetail;
}


/**
 * 分页信息
 */
export interface Pagination {
  pageSize: number;
  totalCount: number;
  pageCount: number;
  pageIndex: number;
}

/**
 * 查询结果
 */
export interface QueryResult {
  result: any[];
  pagination: Pagination;
}

/**
 * 返回结果类型
 */
export interface ResponseInfo {
  returnValue: any;
  message: any[];
  innerDataChange: ChangeDetail[];
  innerVariableChange: ChangeDetail;
}

/**
 * 带RequestInfo的body对象
 */
export interface BodyWithRequestInfo {
  requestInfo: RequestInfo;
  [key: string]: any;
}

/**
 * request option格式
 */
export interface RequestOption {
  body: BodyWithRequestInfo;
  [key: string]: any;
}

/**
 *  异常处理信息接口
 */
export interface IErrorServe {
  show(error: any): any;
}