import { Observable, BehaviorSubject, of, Subject } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';
import { HttpHeaders, HttpRequestConfig, HttpClient } from '@farris/mobile-devkit';

import { BefHttpUtil } from '../../utils/index';
import { FrameworkSessionService } from '../../framework_session_service';
import { BeSessionStorageStrategy } from '../storage-strategies/index';

/**
 * BefSession处理策略类
 */
abstract class BefSessionHandlingStrategy {

  /**
   * 存储策略
   */
  protected storageStrategy: BeSessionStorageStrategy;

  /**
   * 框架Session服务
   */
  protected frmSessionService: FrameworkSessionService;

  /**
   * Http客户端
   */
  protected httpClient: HttpClient;

  /**
   * 创建Session的的EAPI地址
   */
  protected beBaseUrl: string;

  /**
   * 创建BeSession接口地址
   */
  protected beCreateSessionUrl: string;

  /**
   * 关闭BeSession接口地址
   */
  protected beCloseSessionUrl: string;

  /**
   * 框架SessionId（用户的或者功能菜单的）
   */
  protected get frmSessionId(): string {
    return this.frmSessionService.getCurrentSessionId();
  }

  /**
   * 清空的BeSessionId
   */
  protected oldBeSessionId: string;

  /**
   * BeSession是否存在
   */
  private beSessionExisted = false;

  /**
   * BeSession是否存在流
   */
  private beSessionExisted$: BehaviorSubject<boolean>;

  /**
   * 构造函数
   */
  constructor(
    storageStrategy: BeSessionStorageStrategy, frmSessionService: FrameworkSessionService,
    httpClient: HttpClient, beBaseUrl: string
  ) {
    this.storageStrategy = storageStrategy;
    this.frmSessionService = frmSessionService;
    this.httpClient = httpClient;
    this.beBaseUrl = beBaseUrl;
    this.beCreateSessionUrl = `${beBaseUrl}/service/createsession`;
    this.beCloseSessionUrl = `${beBaseUrl}/service/closesession`;
    this.beSessionExisted$ = new BehaviorSubject<boolean>(this.beSessionExisted);
  }

  public abstract getSessionId(): Observable<string>;
  public abstract setSessionId(sessionId): void;
  public abstract clearSessionId(): void;
  public abstract handleRequestHeaders(headers: HttpHeaders): HttpHeaders;
  public abstract handleReponseHeaders(headers: HttpHeaders): void;
  protected abstract getSessionStorageKey(): string;

  /**
   * 获取框架SessionId
   */
  public getFrameworkSessionId() {
    return this.frmSessionId;
  }

  /**
   * 从缓存中获取BeSession
   */
  protected getSessionIdFromStorage() {
    const sessionStorageKey = this.getSessionStorageKey();
    const beSessionId = this.storageStrategy.getItem(sessionStorageKey);
    return beSessionId;
  }

  /**
   * 创建BeSessionId
   */
  protected createSession(): Observable<string> {
    const requestConfig: HttpRequestConfig = {
      responseType: 'text',
      headers: {
        'Content-Type': 'application/json'
      }
    };
    const frmSessionId = this.frmSessionId;
    if (frmSessionId) {
      requestConfig.headers = BefHttpUtil.appendSessionId(requestConfig.headers, this.frmSessionId);
      requestConfig.headers = BefHttpUtil.appendCafRuntimeCommonVariable(requestConfig.headers, this.frmSessionId);
    }
    //创建前判断当前的frmsessionid是不是在当前会话下被清除过
    const ClearItOnce = window.sessionStorage.getItem("ClearItOnce");
    if ('true' !== ClearItOnce  && frmSessionId) {
      return this.clear(frmSessionId).pipe(
        switchMap(() => {
          window.sessionStorage.setItem("ClearItOnce", 'true');
          return this.httpClient.post(this.beCreateSessionUrl, null, requestConfig).pipe(
            tap((beSessionId: string) => {
              this.setSessionId(beSessionId);
              this.setBesessionExisted(true);
            })
          );

        })
      )
    }

    return this.httpClient.post(this.beCreateSessionUrl, null, requestConfig).pipe(
      tap((beSessionId: string) => {
        this.setSessionId(beSessionId);
        this.setBesessionExisted(true);
      })
    );
  }

  clear(formToken) {
    const subject = new Subject();
    if (formToken && window['frmMobileService']) {
      window['frmMobileService'].rtf.func.clearState({ formToken }, () => { subject.next(true) },)
    }
    return subject;
  }

  test() {
    const requestConfig: HttpRequestConfig = {
      responseType: 'text',
      headers: {
        'Content-Type': 'application/json'
      }
    };

    if (this.frmSessionId) {
      requestConfig.headers = BefHttpUtil.appendSessionId(requestConfig.headers, this.frmSessionId);
      requestConfig.headers = BefHttpUtil.appendCafRuntimeCommonVariable(requestConfig.headers, this.frmSessionId);
    }

    return this.httpClient.post(this.beCreateSessionUrl, null, requestConfig).pipe(
      tap((beSessionId: string) => {
        this.setSessionId(beSessionId);
        this.setBesessionExisted(true);
      })
    );
  }

  /**
   * 关闭BeSessionId
   */
  protected closeOldSession(): Observable<boolean> {
    if (!this.oldBeSessionId) {
      return of(true);
    }

    const requestConfig: HttpRequestConfig = {
      responseType: 'text',
      headers: {
        'Content-Type': 'application/json'
      }
    };

    // headers处理
    requestConfig.headers = BefHttpUtil.appendCafRuntimeContext(requestConfig.headers, this.oldBeSessionId);
    requestConfig.headers = BefHttpUtil.appendSessionId(requestConfig.headers, this.oldBeSessionId);
    if (this.frmSessionId) {
      requestConfig.headers = BefHttpUtil.appendCafRuntimeCommonVariable(requestConfig.headers, this.frmSessionId);
    }

    // 无论是否成功，统一置空cleardBeSessionId
    return this.httpClient.post(this.beCloseSessionUrl, null, requestConfig).pipe(
      tap(
        () => {
          this.oldBeSessionId = null;
          return of(true);
        },
        () => {
          this.oldBeSessionId = null;
          return of(true);
        }
      )
    );
  }

  /**
   * 设置BeSession存在状态，并发出通知
   */
  protected setBesessionExisted(beSessionExisted: boolean) {
    if (this.beSessionExisted === beSessionExisted) {
      return;
    }
    this.beSessionExisted = beSessionExisted;
    this.beSessionExisted$.next(beSessionExisted);
  }

  /**
   * 获取BeSession是否存在状态
   */
  public getBeSessionExisted(): Observable<boolean> {
    return this.beSessionExisted$;
  }

}

export { BefSessionHandlingStrategy };
