import { InjectionToken } from '@farris/mobile-devkit';
import { IErrorServe } from './types';

const BE_SERVER_URI_TOKEN = new InjectionToken<string>('@farris/be BE_SERVER_URL');

/**
 * 1、因为bef里，很多类都没有注入，BefSessionHandlingStragegy无法直接注入；
 * 2、通过一个字符串Token来间接做策略选择。
 */
const BE_SESSION_HANDLING_STRATEGY_TOKEN = new InjectionToken<boolean>('@farris/be BE_SESSION_HANDLING_STRATEGY_TOKEN');

/**
 * 注入异常处理service
 */
const BE_ERROR_HANDLER__TOKEN = new InjectionToken<IErrorServe>('@farris/be BE_ERROR_HANDLER__TOKEN')

export { BE_SERVER_URI_TOKEN, BE_SESSION_HANDLING_STRATEGY_TOKEN, BE_ERROR_HANDLER__TOKEN };
